export function addClass(el,name){
  if(hasClass(el,name)){
    return
  }
  let classArr = el.className.split(' ') 
  classArr.push(name)
  el.className = classArr.join(' ')
}

export function hasClass(el,name){
  let reg=new RegExp('(^|\s)'+name+'($|\s)')
  return reg.test(el.classNmae)
}

export function getData(el,name,val) {
  
}

let elemenStyle = document.createElement('div').style
let vendor = (()=>{
  let transformNmaes = {
    webkit: 'webkitTransform',
    Moz: 'MozTransform',
    O: 'OTransform',
    ms: 'msTransform',
    standard: 'transform'
  }

  for(let key in transformNmaes){
    if(elemenStyle[transformNmaes[key]] !== undefined){
      return key
    }
  }
  return false

})()

export function prefixStyle(style) {
  if( vendor === false){
    return false
  }
  if( vendor === 'standard'){
    return style
  }
  return vendor + style.charAt(0).toUpperCase() + style.substr(1)
}


