<template>
  <div class="no-result">
    <div class="no-result-icon"></div>
    <p class="no-result-text">{{title}}</p>
  </div>
</template>

<script>
export default {
  props:{
    title: {
      type: String,
      default: ''
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .no-result
    text-align: center
    .no-result-icon
      width: 80px
      height: 90px
      margin: 0 auto
      background url(logo.png) no-repeat
      background-size: contain
    .no-result-text
      margin-top: 30px
      font-size: $font-size-medium
      color: $color-text-l
</style>



