<template>
  <div class="singer" ref="singer">
    <singer-list :data="singers" @select="selectSinger" ref="singerList"></singer-list>
    <router-view></router-view>
  </div>
</template>

<script>
  import {getSingerList} from '@/api/singer'
  import {getDiscList} from '@/api/recommend'
  import Singer from '@/common/js/singer'
  import SingerList from '@/base/singer-list/singer-list'
  import {mapMutations} from 'vuex'
  import {playlistMixin} from '@/common/js/mixin'
  const HOT_NAME=''
  const HOT_LENGTH=10
  export default {
    mixins:[playlistMixin],
    data() {
      return {
        singers: []
      }
    },
    created() {
      this._getSingerList()
    },
    methods: {
      _getSingerList(){
        getSingerList().then((res)=>{
          this.singers=this._normalizeSingerList(res.data.list)
        })
      },
      _normalizeSingerList(list){
        let map={
          hot:{
            title:'',
            items:[]
          }
        }
        // 
        list.forEach((item,index)=>{
          if(index < HOT_LENGTH){
            map.hot.items.push(new Singer({
              name:item.Fsinger_name,
              id:item.Fsinger_mid
            }))
          }
          //
          if(!map[item.Findex]){
            map[item.Findex]={
              title:item.Findex,
              items: []
            }
          }
          //
          map[item.Findex].items.push(new Singer({
            name:item.Fsinger_name,
            id:item.Fsinger_mid
          }))
        })
        //
        let hot=[]
        let letter=[]

        for(let key in map){
          let value = map[key]
          if(value.title.match(/[a-zA-Z]/)){
            letter.push(value)
          }else if(value.title==='') {
            hot.push(value)
          }
        }
        letter.sort((a, b) => {
          return a.title.charCodeAt(0) - b.title.charCodeAt(0)
        })

        return hot.concat(letter)
      },
      selectSinger(singer){
        this.$router.push({
          path: `/singer/${singer.id}`
        })
        this.setSinger(singer)
      },
      handlePlaylist(list){
        const bottom = list.length > 0 ? '60px' : ''
        this.$refs.singer.style.bottom = bottom
        this.$refs.singerList.refresh()
      },
      ...mapMutations({
        setSinger: 'SET_SINGER'
      })
    },
    components:{
      SingerList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .singer
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
</style>



