<template>
  <div class="rank" ref="rank">
    <scroll class="rank-list" :data="rankList" ref="rankList">
      <ul>
        <li class="item"  v-for="item in rankList" @click="selectItem(item)">
          <div class="icon">
            <img :src="item.picUrl">
          </div>
          <ul class="song-list">
            <li class="song" v-for="(song,index) in item.songList" >
              <span v-html="index+1"></span>
              <span>{{song.singername}}-{{song.songname}}</span>
            </li>
          </ul>
        </li>
      </ul>
      <div class="loading-wrapper" v-show="!rankList.length">
        <loading></loading>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script>
import Scroll from '@/base/scroll/scroll'
import { getRankList } from '@/api/rank'
import {ERR_OK} from '@/api/config'
import Loading from '@/base/loading/loading'
import {mapMutations} from 'vuex'
import {playlistMixin} from '@/common/js/mixin'
export default {
  mixins:[playlistMixin],
  data(){
    return{
      rankList:[]
    }
  },
  components:{
    Scroll,
    Loading
  },
  created(){
    this._getRankList()
  },
  methods: {
    _getRankList(){
      getRankList().then((res)=>{
        if(res.code===ERR_OK){
        this.rankList= res.data.topList
        }
      })
    },
    selectItem(item){
      this.$router.push({
        path: `/rank/${item.id}`
      })
      this.setRank(item)
    },
    handlePlaylist(list){
      const bottom = list.length > 0 ? '60px' : ''
      this.$refs.rank.style.bottom = bottom
      this.$refs.rankList.refresh()
    },
    ...mapMutations({
      setRank: "SET_RANK"
    })
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus"> 
  @import "~@/common/stylus/var"
  .rank
    position fixed
    top 88px
    bottom 0
    width 100%
    background-color $color-background
    .rank-list
      height 100%
      overflow hidden
      .item
        display flex
        height 100px
        padding-top 20px
        margin 0 20px
        &:last-child
          padding-bottom: 20px
        .icon
          flex 0 0 100px
          width 100px
          height 100px
          img 
            width 100px
            height 100px
        .song-list
          flex 1
          display flex
          flex-direction column
          justify-content center
          padding 0 20px
          height 100px
          overflow hidden
          background-color $color-white
          color: $color-text-l
          font-size: $font-size-small
          .song
            no-wrap()
            line-height: 26px
      .loading-wrapper
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



