<template>
  <scroll class="suggest" @scrollEnd="searchMore" @beforeScroll="listScroll" :pullup="pullup" :beforeScroll="beforeScroll" ref="suggest">
    <ul class="suggest-list">
      <li class="suggest-item" v-for="(item,index) in result" @click="selectItem(item)">
        <div class="icon">
          <i class="iconfont" v-html="getIconText(item)"></i>
        </div>
        <div class="name">
          <p v-html="getNameText(item)" class="text"></p>
        </div>
      </li>
      <loading v-show="hasMore" title=""></loading>
    </ul>
    <div v-show="!hasMore && !result.length" class="no-result-wrapper">
      <no-result title=""></no-result>
    </div>
  </scroll>
</template>

<script>
import {search} from '@/api/search'
import {ERR_OK} from '@/api/config'
import {createSong} from '@/common/js/song'
import Scroll from '@/base/scroll/scroll'
import Loading from '@/base/loading/loading'
import NoResult from '@/base/no-result/no-result'
import { mapMutations, mapActions } from 'vuex'
import Singer from '@/common/js/singer'

const TYPE_SINGER = 'singer' 
export default {
  props: {
    query: {
      type: String,
      default: ''
    },
    showSinger: {
      type: Boolean,
      default: true
    }
  },
  data(){
    return {
      page: 1,
      perpage: 20,
      result: [],
      pullup: true,
      hasMore: true,
      beforeScroll: true
    }
  },
  watch: {
    query(){
      this.search()
    }
  },
  methods: {
    //
    refresh(){
      this.$refs.suggest.refresh()
    },
    //
    selectItem(item) {
      if (item.type === TYPE_SINGER) {
        const singer = new Singer({
          id: item.singermid,
          name: item.singername
        })
        this.$router.push({
          path: `/search/${singer.id}`
        })
        this.setSinger(singer)
      } else {
        this.insertSong(item)
      }
      this.$emit('select', item)
    },
    //
    listScroll() {
      this.$emit('listScroll')
    },
    //
    searchMore(){
      if (!this.hasMore) {
        return
      }
      this.page++
      search(this.query, this.page, this.showSinger, this.perpage).then((res) => {
        if (res.code === ERR_OK) {
          this.result = this.result.concat(this._getResult(res.data))
          this._checkMore(res.data)
        }
      })
    },
    _checkMore(data){
      const song = data.song
      if (!song.list.length || (song.curnum + (song.curpage - 1) * this.perpage) >= song.totalnum) {
        this.hasMore = false
      }
    },
    
    //
    search(){
      this.hasMore = true
      this.page = 1
      this.$refs.suggest.scrollTo(0, 0)
      search(this.query,this.page,this.showSinger,this.perpage).then((res)=>{
        if(ERR_OK === res.code){
          this.result = this._getResult(res.data)
          this._checkMore(res.data)
        }
      })
    },
    _getResult(data){
      let ret = []
      
      if (data.zhida && data.zhida.singerid) {
        ret.push({...data.zhida, ...{type: TYPE_SINGER}})
      }
      if (data.song) {
        ret = ret.concat(this._normalizeSongs(data.song.list))
      }
      return ret
    },
    _normalizeSongs(list) {
      let ret = []
      list.forEach((musicData) => {
        ret.push(createSong(musicData))
      })
      return ret
    },
    //
    getIconText(item){
      if(item.type === TYPE_SINGER){
        return "&#xe60f;"
      }else {
        return "&#xe680;"
      }
    },
    getNameText(item){
      if(item.type === TYPE_SINGER){
        return item.singername
      }else {
        return `${item.name}-${item.singer}`
      }
    },
    ...mapActions([
      'insertSong'
    ]),
    ...mapMutations({
      setSinger:'SET_SINGER'
    })
  },
  components: {
    Scroll,
    Loading,
    NoResult
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/var"
  .suggest
    height 100%
    overflow hidden
    .suggest-list
      padding 0 30px
      .suggest-item
        display flex
        align-items center
        padding-bottom 20px
        .icon
          flex 0 0 30px
          width 30px
          .iconfont
            font-size $font-size-medium
            color $color-text-l
        .name
          flex: 1
          font-size: $font-size-medium
          color: $color-text-l
          overflow: hidden
          .text
            no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>
  


