<template>
  <div class="loading">
    <!-- <img src="./loading.gif" alt=""> -->
    <p class="tip">{{tip}}</p>
  </div>
</template>

<script>
export default {
  props:{
    tip:{
      type: String,
      default: 'Loading...'
    }
  }
}
</script>

<style scoped rel='stylesheet/stylus' lang='stylus'>
  @import "~@/common/stylus/var"
  .loading
    text-align center
    img
      width 150px
      height 150px
    p
      line-height 20px
      font-size: $font-size-large-x
      color: $color-theme
</style>



