<template>
  <div class="song-list">
    <ul>
      <li v-for="(song,index) in songs" class="item" @click="selectItem(song,index)">
        <div class="rank" v-show="rank">
          <span class="text iconfont" :class="getRankCls(index)" v-html="getRankText(index)"></span>
        </div>
        <div class="content">
          <div class="name">{{song.name}}</div>
          <p class="desc" v-html="getDesc(song)"></p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    songs: {
      type: Array,
      default: []
    },
    rank: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    getDesc(songs){
      return `${songs.singer}&nbsp;-&nbsp;${songs.album}`
    },
    selectItem(song,index){
      this.$emit('select',song,index)
    },
    getRankCls(index){
      if (index <= 2) {
        return `text${index}`
      }
    },
    getRankText(index){
      if(index==0){
        return "&#xe66d;"
      }
      if(index==1){
        return "&#xe66b;"
      }
      if(index==2){
        return "&#xe66c;"
      } 
      if (index > 2) {
        return index + 1
      }
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/var"
  .song-list
    .item
      display: flex
      align-items: center
      box-sizing: border-box
      height: 64px
      font-size: $font-size-medium
      .rank
        flex 0 0 25px
        width 25px
        margin-right: 30px
        text-align: center
        .text
          color: $color-theme
          font-size: $font-size-large
          &.text0
            color #E0B335
            font-size $font-size-large-x
           &.text1
            color #A2B5C1
            font-size $font-size-large-x
           &.text2
            color #A97B6A
            font-size $font-size-large-x
      .content
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          no-wrap()
          color: $color-text
        .desc
          no-wrap()
          margin-top: 4px
          color: $color-text-l
</style>



