<template>
  <div class="search-box">
    <i class="iconfont search-icon">&#xe960;</i>
    <input type="text" class="box" :placeholder="placeholder" v-model="query" ref="query">
    <i class="iconfont delete-icon" v-show="query" @click="clear">&#xe60a;</i>
  </div>
</template>

<script>
import {debounce} from '@/common/js/util'
export default {
  props: {
    placeholder: {
      type: String,
      default: ''
    }
  },
  data(){
    return {
      query: ''
    }
  },
  created(){
    this.$watch('query', debounce((newQuery) => {
      this.$emit('query', newQuery)
    }, 200))
  },
  methods: {
    clear(){
      this.query = ''
    },
    //
    blur() {
      this.$refs.query.blur()
    },
    setQuery(query) {
      this.query = query
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "~@/common/stylus/var"
  .search-box
    display flex
    align-items center
    height 40px
    width 100%
    padding 0 6px
    box-sizing: border-box
    border-radius: 6px
    background: $color-text-d
    .iconfont
      font-size: 24px
      color: $color-text-l
    .box
      flex: 1
      margin: 0 5px
      line-height: 18px
      background: transparent
      color: $color-text-l
      font-size: $font-size-medium
      outline: 0
      caret-color: $color-text-l
      &::placeholder
        color: $color-text-d
</style>



