<template>
  <div class="progress-bar" ref="progressBar">
    <div class="bar-inner" @click="progressClick">
      <div class="progress" ref="progress"></div>
      <div 
        class="progress-btn-wrapper" 
        ref="progressBtn"
        @touchstart.prevent="progressTouchStart"
        @touchmove.prevent="progressTouchMove"
        @touchend="progressTouchEnd"
      >
        <div class="progress-btn"></div>
      </div>
    </div>
  </div>
</template>

<script>
import {prefixStyle} from '@/common/js/dom'
const progressBtnWidth = 16
const transform = prefixStyle("transform")
export default {
  props:{
    percent:{
      type:Number,
      default:0
    }
  },
  data(){
    return {
      touch:{}
    }
  },
  watch:{
    percent(newPercent){
      if(newPercent >=0 && !this.touch.initiated){
        let duration = this.$refs.progressBar.clientWidth - progressBtnWidth
        let offsetWidth = duration * newPercent 
        this._offsetWidth(offsetWidth)
      }
    }
  },
  methods:{
    setProgressOffset(newPercent){
      if(newPercent >=0 && !this.touch.initiated){
        let duration = this.$refs.progressBar.clientWidth - progressBtnWidth
        let offsetWidth = duration * newPercent 
        this._offsetWidth(offsetWidth)
      }
    },
    progressTouchStart(e){
      this.touch.initiated = true
      this.touch.startX = e.touches[0].pageX
      this.touch.left = this.$refs.progress.clientWidth
    },
    progressTouchMove(e){
      let deltaX = e.touches[0].pageX-this.touch.startX
      let offsetWidth =  Math.min(this.$refs.progressBar.clientWidth - progressBtnWidth,Math.max(0,this.touch.left + deltaX))
      this._offsetWidth(offsetWidth)
    },
    progressTouchEnd(){
      this.touch.initiated = false
      this._triggerPercent()                                                                  
    },
    progressClick(e){
      const rect = this.$refs.progressBar.getBoundingClientRect()
      let offsetWidth = e.pageX - rect.left
      offsetWidth = Math.min(this.$refs.progressBar.clientWidth - progressBtnWidth,Math.max(0,offsetWidth))
      this._offsetWidth(offsetWidth) 
      this._triggerPercent()
    },
    _offsetWidth(offsetWidth){
      this.$refs.progress.style.width = `${offsetWidth}px`
      this.$refs.progressBtn.style[transform] = `translate3d(${offsetWidth}px,0,0)`
    },
    _triggerPercent(){
      this.$emit('percentChange', this._getPercent())
    },
    _getPercent() {
      const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth
      return this.$refs.progress.clientWidth / barWidth
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/var"
  .progress-bar
    height: 30px
    .bar-inner
      position relative
      top 13px
      height 4px
      background-color rgba(0, 0, 0, 0.3)
      .progress
        position: relative
        height: 100%
        background: $color-theme-l
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-text
          border-radius: 50%
          background: $color-theme-l
</style>



