<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" @click="hide">
      <div class="list-wrapper" @click.stop>
        <!-- header -->
        <div class="list-header">
          <div class="iconmode">
            <i class="iconfont">&#xe62c;</i>
          </div>
          <span class="text"></span>
          <div class="iconclear" @click.stop="showConfirm">
            <i class="iconfont">&#xe609;</i>
          </div>
        </div>
        <!-- content -->
        <scroll class="list-content" :data="sequenceList" ref="scroll">
          <transition-group name="list" tag="ul">
            <li class="item" :key="index" v-for="(item,index) in sequenceList" @click="selectItem(item,index)" ref="item">
              <div class="playIcon">
                <i class="iconfont" v-html="getPlayIcon(item)"></i>
              </div>
              <span class="text" v-html="item.name" :class="getCurrentCls(item)"></span>
              <div class="favoriteIcon">
                <i class="iconfont">&#xe608;</i>
              </div>
              <div class="deleteIcon" @click.stop="deleteOne(item)">
                <i class="iconfont">&#xe60a;</i>
              </div>
            </li>
          </transition-group>
        </scroll>
        <!-- operate -->
        <div class="list-operate">
          <div class="add">
            <i class="iconfont">&#xe960;</i>
            <span class="text"></span>
          </div>
        </div>
        <div class="list-close" @click="hide">
          <span></span>
        </div>
        <!-- confirm -->
        <confirm @confirm="clearSongList" text="" ref="confirm"></confirm>
        <!-- addSong -->
      </div>
    </div>
  </transition>
</template>

<script>
import {mapGetters,mapMutations,mapActions} from 'vuex'
import Scroll from '@/base/scroll/scroll'
import {playMode} from '@/common/js/config'
import Confirm from '@/base/confirm/confirm'
export default {
  data(){
    return {
      showFlag: false
    }
  },
  methods:{
    //
    selectItem(item,index){
      if(this.mode === playMode.random){
        index = this.playlist.findIndex((song)=>{
          return song.id === item.id
        })
      }
      this.setCurrentIndex(index)
      this.setPlayingState(true)
    },
    //
    getCurrentCls(item){
      if(item.id === this.currentSong.id){
        return "currentSong"
      }
    },
    getPlayIcon(item){
      if(item.id === this.currentSong.id){
        return "&#xe713;"
      }
      return "&#xe683;"
    },
    //
    hide(){
      this.showFlag = false
    },
    show(){
      this.showFlag = true
      setTimeout(() => {
        this.$refs.scroll.refresh()
        this.scrollTo()
      }, 20)
    },
    //
    scrollTo(){
      const index = this.sequenceList.findIndex((item)=>{
        return this.currentSong.id === item.id
      })
      this.$refs.scroll.scrollToElement(this.$refs.item[index],300)
    },
    //
    deleteOne(item){
      this.deleteSong(item)
      if(!this.playlist.length){
        this.showFlag = false
      }
    },
    showConfirm(){
      this.$refs.confirm.show()
    },
    clearSongList(){
      this.deleteSongList()
      this.showFlag = false
    },
    ...mapActions([
      'deleteSong',
      'deleteSongList'
    ]),
    ...mapMutations({
      setCurrentIndex: 'SET_CURRENT_INDEX',
      setPlayingState: 'SET_PLAYING_STATE'
    })
  },
  computed: {
    ...mapGetters([
      'sequenceList',
      'currentSong',
      'mode',
      'playlist'
    ])
  },
  watch: {
    currentSong(newSong,oldSong){
      if(!this.showFlag || newSong.id === oldSong.id){
        return
      }
      this.scrollTo()
    }
  },
  components: {
    Scroll,
    Confirm
  }

}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">

  @import "~@/common/stylus/var"
  .playlist
    position fixed
    left 0
    right 0
    top 0
    bottom 0
    z-index 200
    background-color $color-text-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    .list-wrapper
      position absolute
      left 0
      bottom 0
      width 100%
      background-color $color-theme
      .list-header
        position relative
        padding 20px 30px 10px 20px
        display flex
        align-items center
        .iconmode
          margin-right 10px
          .iconfont
            font-size 30px
            color $color-theme-l
        .text
          flex 1
          font-size: $font-size-medium
          color $color-white
        .iconclear
          .iconfont
            font-size: $font-size-medium
            color $color-theme-l
      .list-content
        max-height 240px
        overflow hidden
        .item
          display flex
          align-items center
          height 40px
          padding 0 30px 0 20px
          overflow hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .playIcon
            flex 0 0 40px
            width 20px
            font-size: $font-size-small
            color: $color-theme-l
          .text
            flex 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-white
            &.currentSong
              color $color-theme-l
          .favoriteIcon
            margin-right 15px
            .iconfont
              font-size: $font-size-small
              color: $color-theme-l
          .deleteIcon
            .iconfont
              font-size: $font-size-small
              color: $color-theme-l
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display flex
          align-items center
          border-radius: 100px
          border: 1px solid $color-theme-l
          padding 8px 16px
          color $color-white
          .iconfont
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-theme-l
        font-size: $font-size-medium-x
        color: $color-white

</style>



