<template>
  <scroll 
  tag="div" 
  class="singer-list"
  :data="data"
  ref="singerList"
  :probe-type="probeType"
  :listen-scroll="listenScroll"
  @scroll="scroll"
  >
    <ul>
      <li v-for="group in data" class="singer-group" ref="singerGroup"> 
        <uL>
          <li @click="selectSinger(item)" v-for="item in group.items" class="singer-group-item">
            <img class="icon" v-lazy="item.src">
            <span class="name">{{item.name}}</span>
          </li>
        </uL>
      </li>
    </ul>
    <div 
    class="shortcut"
    @touchstart.stop.prevent="onTouchstart"
    @touchmove.stop.prevent="onTouchmove"
    >
      <ul>
        <li 
        v-for="(item, index) in shortcutList" 
        class="shortcut-item" 
        :class="{'current':currentGroup===index}"
        :index="index"
        >
          {{item}}
        </li>
      </ul>
    </div>
    <div class="loading-wrapper" v-show="!data.length">
        <loading></loading>
      </div>
  </scroll>
</template>

<script>
  import Scroll from "@/base/scroll/scroll"
  import Loading from '@/base/loading/loading'
  const HEIGHT=18
  export default {
    props:{
      data:{
        type:Array,
        default:[]
      }
    },
    data(){
      return {
        currentGroup: 0,
        touch: {},
        probeType: 3,
        listenScroll: true,
        scrollY: 0,
        listHeight: []
      }
    },
    activated() {
      setTimeout(() => {
        this.$refs.singerList && this.$refs.singerList.refresh()
      }, 20)
    },
    methods:{
      onTouchstart(e){
        let anchorIndex = e.target.getAttribute('index')
        let firstTouch = e.touches[0]
        this.touch.y1 = firstTouch.pageY
        this.touch.anchorIndex = anchorIndex
        this.scrollTo(anchorIndex)
      },
      onTouchmove(e){
        let firstTouch = e.touches[0]
        this.touch.y2 = firstTouch.pageY
        let delta = (this.touch.y2 - this.touch.y1) / HEIGHT | 0
        let anchorIndex = parseInt(this.touch.anchorIndex) + delta
        this.scrollTo(anchorIndex)
      },
      scrollTo(index){
        if (!index && index !== 0) {
          return
        }
        if (index < 0) {
          index = 0
        } 
        else if (index > this.listHeight.length - 2) {
          index = this.listHeight.length - 2
        }
        this.$refs.singerList.scrollToElement(this.$refs.singerGroup[index], 0)
        this.scrollY = this.$refs.singerList.scroll.y
      },
      calculateHeight(){
        this.listHeight = []
        const groups = this.$refs.singerGroup
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < groups.length; i++) {
          let group = groups[i]
          height += group.clientHeight
          this.listHeight.push(height)
        }
      },
      scroll(pos) {
        this.scrollY = pos.y
      },
      selectSinger(item){
        this.$emit('select', item)
      },
      refresh(){
        this.$refs.singerList.refresh()
      }
    },
    computed:{
      shortcutList(){
        return this.data.map((group) => {
          return group.title.substr(0, 1)
        })
      }
    },
    watch:{
      data(){
        setTimeout(()=>{
          this.calculateHeight()
        },20)
      },
      scrollY(newY) {

        const listHeight = this.listHeight
        if (newY > 0) {
          this.currentGroup = 0
          return
        }

        for (let i = 0; i < listHeight.length - 1; i++) {
          let height1 = listHeight[i]
          let height2 = listHeight[i + 1]
          if (-newY >= height1 && -newY < height2) {
            this.currentGroup = i
            return
          }
        }
   
        this.currentGroup = listHeight.length - 2
      }
    },
    components:{
      Scroll,
      Loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/var"

  .singer-list
    position: relative
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    .singer-group
      .singer-group-item
        display: flex
        align-items: center
        padding: 10px 0 10px 10px
        margin 1px 20px 1px 0
        background-color $color-white
        .icon
          width: 50px
          height: 50px
          border-radius: 50%
        .name
            margin-left: 20px
            color: $color-text
            font-size: $font-size-medium
    .shortcut
      position: absolute
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 10px 0
      border-radius: 10px
      box-sizing border-box
      border 1px solid $color-text-l
      font-family: Helvetica
      .shortcut-item
        text-align center
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme-l
    .loading-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)    
</style>



