<template>
  <div class="player"  v-show="playlist.length">
    <transition 
      name="normal"
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
      @after-leave="afterLeave"
    >
      <div class="normal-player" v-show="fullScreen">
        <!--  -->
        <div class="background">
          <img width="100%" height="100%" :src="currentSong.image" >
        </div>
        <!--  -->
        <div class="top">
          <div class="back" @click="back">
            <i class="iconfont">&#xe646;</i>
          </div>
          <h1 class="title" v-html="currentSong.name"></h1>
          <h2 class="subtitle" v-html="currentSong.singer"></h2>
        </div>
        <!--  -->
        <div class="middle"
          @touchstart.prevent="middleTouchStart"
          @touchmove.prevent="middleTouchMove"
          @touchend="middleTouchEnd"
        >
          <!--  -->
          <div class="middle-l" ref="cd">
            <div class="cd-wrapper" ref="cdWrapper" >
              <div class="cd"  :class="cdCls">
                <img class="image" :src="currentSong.image"> 
              </div>
            </div>

            <div class="playing-lyric-wrapper">
              <div class="playing-lyric">{{playingLyric}}</div>
            </div>
          </div>
          <!--  -->
          <scroll class="middle-r" ref="lyricList">
            <div class="lyric-wrapper">
              <div  v-if="currentLyric">
                <p ref="lyricLine"
                  class="text"
                  :class="{'current': currentLineNum ===index}"
                  v-for="(line,index) in currentLyric.lines"
                >
                  {{line.txt}}
                </p>
              </div>
            </div>
          </scroll>
        </div>
 

        <!--  -->
        <div class="bottom">
          <div class="dot-wrapper">
            <span class="dot" :class="{'active':currentShow==='cd'}"></span>
            <span class="dot" :class="{'active':currentShow==='lyric'}"></span>
          </div>
          <!--  -->
          <div class="progress-wrapper">
            <span class="time time-l">{{format(currentTime)}}</span>
            <div class="progress-bar-wrapper">
              <progress-bar ref="progressBar" :percent="percent" @percentChange="percentChange"></progress-bar>
            </div>
            <span class="time time-r">{{format(currentSong.duration)}}</span>
          </div>
          <!--  -->
          <div class="operators">
            <div class="icon i-left i-mode">
              <i class="iconfont" v-html="changIcon" @click="changeMode"></i>
            </div>
            <div class="icon i-left" :class="disableCls">
              <i class="iconfont" @click="prev">&#xe64f;</i>
            </div>
            <div class="icon i-center" :class="disableCls">
              <i class="iconfont" @click="togglePlaying" v-html="playIcon"></i>
            </div>
            <div class="icon i-right" :class="disableCls">
              <i class="iconfont" @click="next">&#xe64d;</i>
            </div>
            <div class="icon i-right i-favorite" >
              <i class="iconfont">&#xe608;</i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <!-- mini -->
    <transition name="mini">
      <div class="mini-player" v-show="!fullScreen" @click="open">
        <div class="icon">
          <div class="imgWrapper"  :class="cdCls">
          <img width="40px" height="40px" :src="currentSong.image">
          </div>
        </div>
        <div class="text">
          <h2 class="name" v-html="currentSong.name"></h2>
          <p class="desc" v-html="currentSong.singer"></p>
        </div>
        <!--  -->
        <div class="control">
          <progrsss-circle :percent="percent" :radius="radius">
            <i class="iconfont control-play" @click.stop="togglePlaying" v-html="playIcon"></i>
          </progrsss-circle>
        </div>
        <div class="control" @click.stop="showPlaylist">
          <i class="iconfont control-list">&#xe717;</i>
        </div>
      </div>
    </transition>
    <playlist ref="playlist"></playlist>
    <audio ref="audio" @play="ready" @error="error" @timeupdate="updateTime"
           @ended="end"></audio>
  </div>
</template>

<script>
  import {mapGetters,mapMutations} from 'vuex'
  import animations from 'create-keyframe-animation'
  import {prefixStyle} from '@/common/js/dom'
  import ProgressBar from '@/base/progress-bar/progress-bar'
  import ProgrsssCircle from '@/base/progress-circle-bar/progress-circle-bar'
  import {playMode} from '@/common/js/config'
  import {shuffle} from '@/common/js/util'
  import Lyric from 'lyric-parser'
  import Scroll from '@/base/scroll/scroll'
  import Playlist from '@/components/playlist/playlist' 
  const transform = prefixStyle('transform')
  const transition = prefixStyle('transition')
  export default {
    data(){
      return{
        songReady:false,
        currentTime:0,
        radius: 32,
        currentLyric: null,
        currentLineNum: 0,
        touch:{},
        currentShow:'cd',
        playingLyric: ''
      }
    },
    computed:{
      playIcon(){
        return this.playing?'&#xe713;':'&#xe627;'
      },
      changIcon(){
        if(this.mode === playMode.sequence){
          return '&#xe62c;'
        }else if(this.mode === playMode.loop){
          return '&#xe637;'
        }else{
          return '&#xe630;'
        }
      },
      cdCls(){
        return this.playing?'play':'play pause'
      },
      disableCls(){
        return this.songReady?'':'disable'
      },
      percent(){
        return this.currentTime/this.currentSong.duration
      },
      ...mapGetters([
        'playlist',
        'fullScreen',
        'currentSong',
        'playing',
        'currentIndex',
        'mode',
        'playlist',
        'sequenceList'
      ])
    },
    watch:{
      currentSong(newSong, oldSong) {
        if (!newSong.id || !newSong.url || newSong.id === oldSong.id) {
          return
        }
        this.songReady = false
        this.canLyricPlay = false
        if (this.currentLyric) {
          this.currentLyric.stop()
          // null
          this.currentLyric = null
          this.currentTime = 0
          this.playingLyric = ''
          this.currentLineNum = 0
        }
        this.$refs.audio.src = newSong.url
        this.$refs.audio.play()
        this.getLyric()
      },
      playing(newPlaying){
         if (!this.songReady) {
          return
        }
        const audio = this.$refs.audio
        this.$nextTick(() => {
          newPlaying ? audio.play() : audio.pause()
        })
      },
      fullScreen(newVal) {
        if (newVal) {
          setTimeout(() => {
            this.$refs.lyricList.refresh()
            this.$refs.progressBar.setProgressOffset(this.percent)
          }, 20)
        }
      }
    },
    methods:{
      //
      back(){
        this.setFullScreen(false)
      },
      open(){
        this.setFullScreen(true)
      },
      //
      togglePlaying(){
        if(!this.songReady){
          return
        }
        this.setPlayingState(!this.playing)
        if(this.currentLyric){
          this.currentLyric.togglePlay()
        }
      },
      next(){
        if(!this.songReady){
          return
        }
        if(this.playlist.length === 1){
          this.loop()
          return
        }
        let index = this.currentIndex + 1
        if(index === this.playlist.length){
          index = 0
        }
        this.setCurrentIndex(index)
        if(!this.playing){
          this.togglePlaying()
        }
        this.songReady = false
      },
      prev(){
        if(!this.songReady){
          return
        }
        if(this.playlist.length === 1){
          this.loop()
          return
        }
        let index = this.currentIndex - 1
        if(index === -1){
          index = this.playlist.length - 1
        }
        this.setCurrentIndex(index)
        if(!this.playing){
          this.togglePlaying()
        }
        this.songReady = false
      },
      ready(){
        setTimeout(() => {
          this.songReady = true
        }, 500)
      },
      error(){
        this.songReady = true
      },
      //
      updateTime(e){
        this.currentTime = e.target.currentTime
      },
      percentChange(newPercent){
        let newTime = this.currentSong.duration * newPercent
        this.$refs.audio.currentTime = newTime
        if(this.currentLyric){
          this.currentLyric.seek(newTime*1000)
        }
      },
      format(time){// 
        time = time | 0
        let min = time / 60 |0
        let second = this.pad(time % 60)
        return `${min}:${second}`
      },
      pad(num,n=2){// 0
        let len = num.toString().length      
        while(len<n){
          num = '0' + num
          len++
        }
        return num
      },
      //
      changeMode(){
        let mode = (this.mode+1)%3
        let list = null
        this.setPlayMode(mode)
        if(mode === playMode.random){
          list = shuffle(this.playlist)
        } else {
          list = this.sequenceList
        }
        this.resetCurrentIndex(list)
        this.setPlaylist(list)
      },
      resetCurrentIndex(list){
        let index = list.findIndex((item)=>{
          return this.currentSong.id === item.id
        })
        this.setCurrentIndex(index)
      },
      //
      end(){
        if(this.mode === playMode.loop){
          this.loop()
        }else(
          this.next()
        )
      },
      loop(){
        this.$refs.audio.currentTime = 0
        this.$refs.audio.play()
        if(this.currentLyric){
          this.currentLyric.seek(0)
        }
        if(this.playing===false){
          this.playing=true
        }
      },
      //
      getLyric(){
        this.currentSong.getLyric().then((res)=>{
          this.currentLyric = new Lyric(res,this.handleLyric)
          if(this.playing){
            this.currentLyric.play()
          }
        })
      },
      handleLyric({lineNum, txt}){
        if (!this.$refs.lyricLine) {
          return
        }
        this.currentLineNum = lineNum
        if (lineNum > 5) {
          let lineEl = this.$refs.lyricLine[lineNum - 5]
          this.$refs.lyricList.scrollToElement(lineEl, 1000)
        } else {
          this.$refs.lyricList.scrollTo(0, 0, 1000)
        }
        this.playingLyric = txt
      },
      //touch
      middleTouchStart(e){
        this.touch.initiated = true
        this.touch.startX = e.touches[0].pageX
        this.touch.startY = e.touches[0].pageY 
        this.touch.moved = false
      },
      middleTouchMove(e){
        if (!this.touch.initiated) {
          return
        }
        
        let deltaX = e.touches[0].pageX - this.touch.startX
        let deltaY = e.touches[0].pageY - this.touch.startY
        
        if (Math.abs(deltaY) > Math.abs(deltaX)) {
          return
        }

        if (!this.touch.moved) {
          this.touch.moved = true
        }
        const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
        const offsetWidth = Math.min(0, Math.max(-window.innerWidth, left + deltaX))
        this.touch.percent = Math.abs(offsetWidth/window.innerWidth)
        this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
        this.$refs.lyricList.$el.style[transition]='all 0'
        this.$refs.cd.style.opacity = 1 - this.touch.percent
        this.$refs.cd.style[transition]='all 0'

      },
      middleTouchEnd(e){
        if (!this.touch.moved) {
          return
        }
        let offsetWidth
        let opacity
        if(this.currentShow === 'cd') {
          if(this.touch.percent > 0.1){
            this.currentShow = 'lyric'
            offsetWidth = -window.innerWidth
            opacity = 0
          }else {
            offsetWidth = 0
            opacity = 1
          }
        } else {
          if(this.touch.percent <0.9){
            this.currentShow = 'cd'
            offsetWidth = 0
            opacity = 1
          }else {
            offsetWidth = -window.innerWidth
            opacity = 0
          }
        }
        this.$refs.lyricList.$el.style[transform] = `translate3d(${offsetWidth}px,0,0)`
        this.$refs.lyricList.$el.style[transition]='all 600ms'
        this.$refs.cd.style.opacity = opacity
        this.$refs.cd.style[transition]='all 600ms'
        this.touch.initiated = false

      },
      //
      showPlaylist(){
        this.$refs.playlist.show()
      },
      // 
      enter(el, done) {
        const {x, y, scale} = this._getPosAndScale()

        let animation = {
          0: {
            transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
          },
          60: {
            transform: `translate3d(0,0,0) scale(1.1)`
          },
          100: {
            transform: `translate3d(0,0,0) scale(1)`
          }
        }

        animations.registerAnimation({
          name: 'move',
          animation,
          presets: {
            duration: 400,
            easing: 'linear'
          }
        })

        animations.runAnimation(this.$refs.cdWrapper, 'move', done)
      },
      afterEnter() {
        animations.unregisterAnimation('move')
        this.$refs.cdWrapper.style.animation = ''
      },
      leave(el, done) {
        this.$refs.cdWrapper.style.transition = 'all 0.4s'
        const {x, y, scale} = this._getPosAndScale()
        this.$refs.cdWrapper.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
        this.$refs.cdWrapper.addEventListener('transitionend',done)
      },
      afterLeave() {
        this.$refs.cdWrapper.style.transition = ''
        this.$refs.cdWrapper.style[transform] = ''
      },
      _getPosAndScale() {
        const targetWidth = 40
        const paddingLeft = 40
        const paddingBottom = 30
        const paddingTop = 80
        const width = window.innerWidth * 0.8
        const scale = targetWidth / width
        const x = -(window.innerWidth / 2 - paddingLeft)
        const y = window.innerHeight - paddingTop - width / 2 - paddingBottom
        return {
          x,
          y,
          scale
        }
      }, 
      ...mapMutations({
        setFullScreen:'SET_FULL_SCREEN',
        setPlayingState:'SET_PLAYING_STATE',
        setCurrentIndex:'SET_CURRENT_INDEX',
        setPlayMode:'SET_PLAY_MODE',
        setPlaylist:'SET_PLAYLIST'
      })
    },
    components:{
      ProgressBar,
      ProgrsssCircle,
      Scroll,
      Playlist
    }
  }
</script>

<style scoped lang='stylus' rel='stylesheet/stylus'>
  @import "~@/common/stylus/var" 
  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: #555
      .background
        position absolute
        left 0
        top 0
        width 100%
        height 100%
        z-index -1
        opacity 0.6
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 0
          left: 6px
          z-index: 50
          .iconfont
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme-l
        .title
          width: 70%
          margin: 0 auto
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-white
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-white
      .middle
        position fixed
        width: 100%
        top: 80px
        bottom: 170px
        white-space: nowrap
        //
        .middle-l
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 60%
          .cd-wrapper
            position: absolute
            left: 20%
            top: 0
            width: 60%
            box-sizing: border-box
            height: 100%
            .cd
              width: 100%
              height: 100%
              border-radius: 50%
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                box-sizing: border-box
                border-radius: 50%
                border: 10px solid rgba(255, 255, 255, 0.1)
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
          .playing-lyric-wrapper
            width: 80%
            margin: 30px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color: $color-white
        //
        .middle-r
          width 100%
          height 100%
          overflow: hidden
          display: inline-block
          vertical-align: top
          .lyric-wrapper
            width: 80%
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: $color-text-lll
              font-size: $font-size-medium
              &.current
                color: $color-white
      .bottom
        position: absolute
        bottom: 50px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-text-l
            &.active
              width: 20px
              border-radius: 5px
              background: $color-white
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 10px 0
          .time
            color: $color-white
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme-l
            &.disable
              color: $color-text-ll
            i
              font-size: 40px
          .i-mode
            i
              font-size: 30px
          .i-favorite
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 50px
          .i-right
            text-align: left
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.4s
        .top, .bottom
          transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: $color-white
      border-top 1px solid #ccc
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        height: 40px
        padding: 0 10px 0 20px
        .imgWrapper
          height: 100%
          width: 100%
          img
            border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-theme-l
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-theme-l
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .iconfont
          display block
          font-size: 30px
          color $color-theme-l
        .control-play
          position absolute
          top 2px
          left 1px

        
  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>



