<template>
  <div id='recommend'>
    <scroll class="recommend-content" :data="discList" ref='scroll'>
      <div>
        <div class="slider-warp" v-if='recommend.length'>
          <div class="slider-content">
            <slider ref="slider">
              <div v-for='item in recommend'>
                <a :href='item.linkUrl'>
                  <img @load="_loadImage" :src="item.picUrl"/>
                </a>
              </div>
            </slider>
          </div>
        </div>
        <div class="recommend-list">
          <h1 class="list-title"></h1>
          <ul>
            <li v-for="item in discList" class="item">
              <div class="icon">
                <img width="60" height="60" v-lazy="item.imgurl">
              </div>
              <div class="text">
                <h2 class="name" v-html="item.creator.name"></h2>
                <p class="desc" v-html="item.dissname"></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-wrapper" v-show="!discList.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script>
  import {getRecommend} from '@/api/recommend'
  import {getDiscList} from '@/api/recommend'
  import {ERR_OK} from '@/api/config'
  import Slider from '@/base/slider/slider'
  import Scroll from '@/base/scroll/scroll'
  import Loading from '@/base/loading/loading'
  export default {
    data(){
      return {
        recommend: [],
        discList: []
      }
    },
    created(){
      this._getRecommend()
      this._getDiscList()
    },
    activated() {
      setTimeout(() => {
        this.$refs.slider && this.$refs.slider.refresh()
        this.$refs.scroll && this.$refs.scroll.refresh()
      }, 20)
    },
    methods:{
      _getRecommend(){
        getRecommend().then((res)=>{
          if(ERR_OK===res.code){
            this.recommend=res.data.slider
          }
        })
      },
      _getDiscList(){
        getDiscList().then((res)=>{
          if(ERR_OK===res.code){
            this.discList=res.data.list
          }
        })
      },
      _loadImage() {
        if (!this.checkloaded) {
          this.checkloaded = true
          setTimeout(() => {
            this.$refs.scroll.refresh()
          }, 20)
        }
      }
    },
    components:{
      Slider,
      Scroll,
      Loading
    }
  }
</script>

<style scoped rel='stylesheet/stylus' lang='stylus'>
  @import "~@/common/stylus/var"
  #recommend
    position fixed
    top 88px
    bottom 0
    width 100%
    background-color $color-background
    .recommend-content
      height 100%
      overflow hidden
      .slider-warp
        position relative
        width 100%
        height 0
        padding-top 40%
        overflow hidden
        .slider-content
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
      .recommend-list
        .list-title
          height: 60px
          line-height: 60px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          margin: 0 0 10px
          padding: 5px 20px
          background-color #fff
          .icon
            flex: 0 0 auto
            width: 60px
            padding-right: 20px
          .text
            flex: 1
            display: flex
            flex-direction: column
            justify-content: center
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-l
      .loading-wrapper
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



