import * as types from './mutation-types'
import {shuffle} from '@/common/js/util'
import {playMode} from '@/common/js/config'
import {saveSearch,deleteSearch,clearSearch} from '@/common/js/cache'

function findIndex(list, song) {
  return list.findIndex((item) => {
    return item.id === song.id
  })
}

export const selectPlay = function ({commit,state},{list,index}){
  commit(types.SET_SEQUENCE_LIST,list)
  if (state.mode === playMode.random) {
    let randomList = shuffle(list)
    commit(types.SET_PLAYLIST,randomList)
    index = findIndex(randomList, list[index])
  }else {
    commit(types.SET_PLAYLIST,list)
  }
  commit(types.SET_CURRENT_INDEX,index)
  commit(types.SET_FULL_SCREEN,true)
  commit(types.SET_PLAYING_STATE,true)

}

export const randomPlay = function ({commit,state},{list,index}){
  commit(types.SET_PLAY_MODE, playMode.random)
  let randomList = shuffle(list)
  commit(types.SET_PLAYLIST,randomList)
  commit(types.SET_SEQUENCE_LIST,list)
  commit(types.SET_CURRENT_INDEX,0)
  commit(types.SET_FULL_SCREEN,true)
  commit(types.SET_PLAYING_STATE,true)
  
}

export const insertSong = function ({commit, state}, song) {

  let playlist = state.playlist.slice()
  let sequenceList = state.sequenceList.slice()
  let currentIndex = state.currentIndex
  let currentSong = playlist[currentIndex]
  currentIndex++

  let songIndex = findIndex(playlist ,song)
  playlist.splice(currentIndex, 0, song)


  if (songIndex > -1) {
    if (currentIndex > songIndex) {
      playlist.splice(songIndex, 1)
      currentIndex--
    } else {
      playlist.splice(songIndex+1, 1)
    }
  }

  let SCurrentIndex = findIndex(sequenceList ,currentSong )
  let SSongIndex = findIndex(sequenceList , song )
  SCurrentIndex++
  sequenceList.splice(SCurrentIndex ,0 ,song )
 

  if (SSongIndex > -1) {
    if (SCurrentIndex > SSongIndex) {
      sequenceList.splice(SSongIndex, 1)
    } else {
      sequenceList.splice(SSongIndex+1, 1)
    }
  }



  commit(types.SET_PLAYLIST, playlist)
  commit(types.SET_SEQUENCE_LIST, sequenceList)
  commit(types.SET_CURRENT_INDEX, currentIndex)
  commit(types.SET_FULL_SCREEN, true)
  commit(types.SET_PLAYING_STATE, true)

}


export const saveSearchHistory = function ({commit}, query) {
  commit(types.SET_SEARCH_HISTORY, saveSearch(query))
}

export const deleteSearchHistory = function ({commit}, query) {
  commit(types.SET_SEARCH_HISTORY, deleteSearch(query))
}

export const clearSearchHistory = function ({commit}, query) {
  commit(types.SET_SEARCH_HISTORY,  clearSearch(query))
}

export const deleteSong = function ({commit, state}, song) {
  
    let playlist = state.playlist.slice()
    let sequenceList = state.sequenceList.slice()
    let currentIndex = state.currentIndex

    let pIndex = findIndex(playlist,song)
    playlist.splice(pIndex,1)
    let sIndex = findIndex(sequenceList,song)
    sequenceList.splice(sIndex,1)
    if(currentIndex > pIndex || currentIndex === playlist.length){
      currentIndex--
    }
    commit(types.SET_PLAYLIST, playlist)
    commit(types.SET_SEQUENCE_LIST, sequenceList)
    commit(types.SET_CURRENT_INDEX, currentIndex)
  
    if (!playlist.length) {
      commit(types.SET_PLAYING_STATE, false)
    } else {
      commit(types.SET_PLAYING_STATE, true)
    }
}

export const deleteSongList = function({commit}){
  commit(types.SET_PLAYLIST, [])
  commit(types.SET_SEQUENCE_LIST, [])
  commit(types.SET_CURRENT_INDEX, -1)
  commit(types.SET_PLAYING_STATE, false)
}


